body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.card {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-flow: column;
	        flex-flow: column;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	height: 100%;
}

.card-container-single {
	max-width: 40vw;
}

.card-container {
	-webkit-flex: 1 1 20%;
	        flex: 1 1 20%;
	margin: 0 2vw;
}

.chip {
  margin: 1vh 2vw;
  -webkit-flex: 1 1 1;
          flex: 1 1 1;
}

.chip-array {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-flow: wrap;
          flex-flow: wrap;
}

.d-flex {
  display: -webkit-flex;
  display: flex;
}

.icon {
	-webkit-flex: 1 1 20%;
	        flex: 1 1 20%;
	width: 10vw;
	text-align: center;
}

.project-img {
	height: 20vh;
}

.story-button {
	text-align: center;
	margin: 5vh 0;
}

.white {
	color: #fff;
}

#about-container {
  display: -webkit-flex;
  display: flex;
  margin: 10vh 10vw;
  -webkit-justify-content: center;
          justify-content: center;
}

#about-image {
  -webkit-flex: 1 1 30%;
          flex: 1 1 30%;
  margin-bottom: 3vh;
}

#about-text {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-flex: 1 1 50%;
          flex: 1 1 50%;
  padding-left: 3vw;
}

#contact-container {
	display: -webkit-flex;
	display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
  -webkit-align-content: center;
          align-content: center;
  -webkit-align-items: center;
          align-items: center;
  margin: 10vh 10vw;
}

#contacts-container {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	-webkit-align-content: space-between;
	        align-content: space-between;
	margin-top: 5vh;
}

#me {
  max-width: 70vw;
	max-height: 70vh;
  float: right;
  border-radius: 5px;
	object-fit: cover;
}

#project-container {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-flow: column;
	        flex-flow: column;
  margin: 10vh 10vw;
	-webkit-align-items: center;
	        align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

#projects-container {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	-webkit-align-content: space-between;
	        align-content: space-between;
	margin-top: 5vh;
}

#skill-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  width: 40vw;
}

#skills-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
  -webkit-align-content: center;
          align-content: center;
  -webkit-align-items: center;
          align-items: center;
  margin: 10vh 10vw;
}

@media (max-width: 1110px) {
  #skill-container {
    width: 90vw;
  }
}

@media(max-width: 1024px) {
	#about-container {
    -webkit-flex-flow: column;
            flex-flow: column;
    margin: 5vh 5vw;
  }

	#me {
		float: none;
		max-width: 100vw;
		margin-bottom: 3vh;
	}
}

@media (max-width: 900px) {
  #about-image {
    text-align: center;
  }

  #about-text {
    padding-left: 0;
  }

	#contacts-container {
		-webkit-flex-flow: column;
		        flex-flow: column;
		width: 100%;
		-webkit-justify-content: center;
		        justify-content: center;
		-webkit-align-content: center;
		        align-content: center;
		-webkit-align-items: center;
		        align-items: center;
	}

	#projects-container {
		-webkit-flex-flow: column;
		        flex-flow: column;
	}

	.card-container {
		margin-bottom: 3vh;
	}

	.card-container-single {
		max-width: none;
	}

	.icon {
		-webkit-flex: 1 1 100%;
		        flex: 1 1 100%;
		width: 100%;
	}
}

